import React, {useState} from "react"
import { Text, Button, SimpleGrid } from "@chakra-ui/core";

import dataJson from "../static/data.json"

import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => {
	const [start , setStart] = useState(false)
	const [finish , setFinish] = useState(false)
	const [result , setResult] = useState('')

	const [page, setPage] = useState(0);
	const [countMap, setCountMap] = useState({})
	const [checkFuckingLoser, setCheckFuckingLoser] = useState([])
	 
	const  order = {
		"가슴매니아" : 1 ,
		"노인성애자" : 2 ,
		"노출광" : 3 ,
		"야외플매니아" : 4 ,
		"새디스트" :5 ,
		"마조히스트" :6 ,
		"남근매니아" :7 ,
		"나르시시스트" : 8 ,
		"야동수집가" : 9 ,
		"위선자" : 10,
		default: Infinity 
  } 

  const onReset = () => {
	setStart(false)
	setPage(0)
	setCountMap({})
	setCheckFuckingLoser([])
	setResult('')
  }

  const onChange = (listCount, index) => {
		if(page < 12){
			//count each value
			listCount.forEach((countName) => {
				let variable = 1
				if (countName[0] === '-'){
						variable *= -1;
						countName = countName.substring(1)
				}
					

				if(countMap[countName] !== undefined){
						setCountMap({
							...countMap,
							[countName] : (countMap[countName]+(variable))
						})
				}
				else{
						setCountMap({
							...countMap,
							[countName] : 1
						})
						
				}
			})
			setCheckFuckingLoser([...checkFuckingLoser, index])
			console.log(countMap)
			
			setPage(page+1)
			
			if( page + 1 === 12) {
				const loser = checkFuckingLoser.reduce((addedArray, item ) => {
					if(addedArray.length === 0 ) {
							addedArray.push(item)
					}
					else{
							if(addedArray[0] !== item){
								addedArray.push(item)
							}
					}
					return addedArray
				}, []).length 
				if(loser > 1){
					const counts = Object.entries(countMap);
					counts.sort((a,b) => {
							if(a[1] > b[1]) return -1
							else if(a[1] < b[1]) return 1
							else {
								return (order[a[0]] || order.default) - (order[b[0]] || order.default);
							}
					})
					setResult(counts[0][0])
				}
				else {
					setResult('게으름뱅이')
				}
			}
			
		}


	}
	console.log(result)
	return (
		
		
		<Layout>
			<SEO title="Home" />
			{!start ?
				<>
					<Text fontSize="xl" my="200px"> 페티쉬 테스트에 오신걸 환영합니다 </Text>
					<Text fontSize="lg"> 여러분의 페티쉬를 심리테스트로 인해 알려드리죠 후후후</Text>
					<Button size="lg" my="50px" onClick={() => {setStart(!start)}}>
						자 시작해볼까요?
					</Button>
				</>
			:
				
				<>
				{
					page < 12 ?
						<>
							<Text
								position="absolute"
								color="#ff007642"
								fontSize="200px"
								left="20px"
								 mt="20px" > Q </Text>
							<Text fontSize="30px" my="150px" > {dataJson[page].question}  </Text>
						
							<SimpleGrid columns={2} spacing={5}>

							{dataJson[page].answers.map((answer, index) => {
								return (
									<Button 
										key={index}
										bg="#ff69b4" 
										height="80px" 
										w="100%" 
										color="white"
										borderRadius="10px"
										whiteSpace="break-spaces"
										onClick={() => {
											onChange(answer.value, index)
										}
										}>
										{answer.label}
									</Button>
								)
							})}
							</SimpleGrid>
						</>
					:
						<>
							<Text fontSize="xl" > 결국 당신도 똑같은 남자였어 이런 붼태 {result}... (부끄부끄)</Text>
							
							<Button bg="#ff69b4" height="300px" w="300px" onClick={onReset}> 
							근데 나 또할래
							</Button>
						</>
				}
					
				</>
			}
			
		</Layout>
	)
}

export default IndexPage
